import React from "react";
import * as yup from "yup";
import { Formik, Form } from "formik";
import { MyInput } from "./MyInput";
import { ButtonToggle } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import { changePassword } from "../redux/action/changeInformUser";

const ProfileChangePassword = (props) => {
  const dispatch = useDispatch();

  const { userInform } = useSelector((state) => state.userReducer);

  const validationPassword = yup.object({
    password: yup
      .string()
      .min(6, "Mt khu qu ngn")
      .required("Vui lng nhp mt khu"),

    password2: yup
      .string()
      .oneOf([yup.ref("password")], "Mt khu khng ging mt khu  nhp")
      .required("Vui lng nhp li mt khu"),
  });

  return (
    <div className="Profile">
      <div id="rightProfile">
        <Formik
          const
          initialValues={{
            fullName: userInform.fullName,
            birthday: userInform.birthday,
            email: userInform.email,
            studentID: userInform.studentID,
            phoneNumber: userInform.phoneNumber,
            password: "",
            password2: "",
            role: userInform.role,
          }}
          validationSchema={validationPassword}
          onSubmit={(values) => dispatch(changePassword(values))}
        >
          {({ handelSubmit }) => (
            <div>
              <Form className="formikProfile">
                <p hidden>H v Tn:</p>
                <MyInput
                  id="fullName"
                  type="text"
                  name="fullName"
                  variant="outlined"
                  hidden
                />
                <p hidden>Ngy sinh:</p>
                <MyInput
                  id="birthday"
                  type="date"
                  name="birthday"
                  variant="outlined"
                  hidden
                />
                <p hidden>Email:(Khng th thay i)</p>
                <MyInput
                  id="email"
                  type="email"
                  name="email"
                  variant="outlined"
                  hidden
                />
                <p hidden>M s sinh vin:</p>
                <MyInput
                  id="studentID"
                  type="text"
                  name="studentID"
                  variant="outlined"
                  hidden
                />
                <p hidden>S in thoi:</p>
                <MyInput
                  id="phoneNumber"
                  type="text"
                  name="phoneNumber"
                  variant="outlined"
                  hidden
                />
                <p>Mt khu mi:</p>
                <MyInput
                  id="password"
                  type="password"
                  name="password"
                  variant="outlined"
                />
                <p>Xc nhn mt khu:</p>
                <MyInput
                  id="password2"
                  type="password"
                  name="password2"
                  variant="outlined"
                />
                <ButtonToggle
                  id="accept"
                  color="primary"
                  onClick={handelSubmit}
                >
                  Xc nhn
                </ButtonToggle>
              </Form>
            </div>
          )}
        </Formik>
      </div>
    </div>
  );
};

export default ProfileChangePassword;
