import React from "react";
import { Formik, Form } from "formik";
import { MyInput } from "./MyInput";
import { ButtonToggle } from "reactstrap";
import "../css/Profile.css";
import { useSelector, useDispatch } from "react-redux";
import * as yup from "yup";
import { changeInform } from "../redux/action/changeInformUser";

const ProfileInform = (props) => {
  const { userInform } = useSelector((state) => state.userReducer);
  const dispatch = useDispatch();

  const validationSchema = yup.object({
    fullName: yup
      .string()
      .required("Vui lng nhp y  h tn")
      .matches(/^([^0-9]*)$/, "Tn ch bao gm k t t a-z")
      .max(30, "Tn qu di")
      .min(5, "Tn qu ngn"),

    birthday: yup.string().required("Vui lng nhp ngy sinh"),

    email: yup
      .string()
      .email("Email khng hp l")
      .required("Vui lng nhp email"),

    studentID: yup
      .string()
      .required("Vui lng nhp m s sinh vin")
      .matches(/^([^a-z]*)$/, "M s sinh vin ch gm 7 s t 0-9")
      .max(7, "M s sinh vin bao gm 7 s")
      .min(7, "M s sinh vin bao gm 7 s"),

    phoneNumber: yup
      .string()
      .required("Vui lng nhp m s in thoi")
      .matches(/^([^a-z]*)$/, "S in thoi ch gm 10 s t 0-9")
      .max(10, "S in thoi bao gm 10 s")
      .min(10, "S in thoi bao gm 10 s"),
  });

  return (
    <div className="Profile">
      <div id="rightProfile">
        <Formik
          initialValues={{
            fullName: userInform.fullName,
            birthday: userInform.birthday,
            email: userInform.email,
            studentID: userInform.studentID,
            phoneNumber: userInform.phoneNumber,
            password: userInform.password,
            role: userInform.role,
          }}
          validationSchema={validationSchema}
          onSubmit={(values) => dispatch(changeInform(values))}
        >
          {({ handelSubmit }) => (
            <div>
              <Form className="formikProfile">
                <p>H v Tn:</p>
                <MyInput
                  id="fullName"
                  type="text"
                  name="fullName"
                  variant="outlined"
                />
                <p>Ngy sinh:</p>
                <MyInput
                  id="birthday"
                  type="date"
                  name="birthday"
                  variant="outlined"
                />
                <p>Email:(Khng th thay i)</p>
                <MyInput
                  id="email"
                  type="email"
                  name="email"
                  variant="outlined"
                  disabled
                />
                {localStorage.getItem("role") === "student"? (
                <p>M s sinh vin:</p>
                ) : null}
                {localStorage.getItem("role") === "admin"? (
                <p>M ging vin:</p>
                ) : null}
                <MyInput
                  id="studentID"
                  type="text"
                  name="studentID"
                  variant="outlined"
                />
                <p>S in thoi:</p>
                <MyInput
                  id="phoneNumber"
                  type="text"
                  name="phoneNumber"
                  variant="outlined"
                />
                <p hidden>Mt khu</p>
                <MyInput
                  id="password"
                  type="text"
                  name="password"
                  variant="outlined"
                  hidden
                />
                <ButtonToggle
                  id="accept"
                  color="primary"
                  onClick={handelSubmit}
                >
                  Cp nht thng tin
                </ButtonToggle>
              </Form>
            </div>
          )}
        </Formik>
      </div>
    </div>
  );
};

export default ProfileInform;
