import React from "react";
import { Formik, Form } from "formik";
import { MyInput } from "./MyInput";
import "../css/SignIn.css";
import * as yup from "yup";
import { signIn } from "../redux/action/userAction";
import { useDispatch } from "react-redux";

const validationSchema = yup.object({
  email: yup
    .string()
    .email("Email khng hp l")
    .required("Vui lng nhp email"),

  password: yup.string().required("Vui lng nhp mt khu"),
});

const SignIn = (props) => {
  const dispatch = useDispatch();

  const handleSuccess = () => {
    props.history.replace("/");
  };

  return (
    <div>
      <Formik
        const
        initialValues={{
          email: "",
          password: "",
        }}
        validationSchema={validationSchema}
        onSubmit={(values) => dispatch(signIn(values, handleSuccess))}
      >
        {({ handelSubmit }) => (
          <div className="signInBody">
            <div className="toSignIn">
              <div className="formSignIn">
                <h2> ng nhp </h2>
                <img src="../img/2.png" />
                <Form className="Formik">
                  <MyInput type="email" name="email" label="Email" />
                  <MyInput type="password" name="password" label="Mt khu" />
                  <button
                    id="signIn1"
                    type="submit"
                    name="SignIn"
                    onClick={handelSubmit}
                  >
                    ng nhp
                  </button>
                </Form>
                <button
                  id="signIn2"
                  type="submit"
                  name="Back"
                  value="ng k"
                  onClick={() => props.history.push("/signup")}
                >
                  ng k
                </button>
              </div>
            </div>
          </div>
        )}
      </Formik>
    </div>
  );
};

export default SignIn;
