import React from "react";
import { Formik, Form } from "formik";
import { MyInput } from "./MyInput";
import * as yup from "yup";
import "../css/SignUp.css";
import { firebaseApp } from "../components/Firebase";
import Swal from "sweetalert2";
import { useDispatch } from "react-redux";
import { signUp } from "../redux/action/userAction";

const validationSchema = yup.object({
  fullName: yup
    .string()
    .required("Vui lng nhp y  h tn")
    .matches(/^([^0-9]*)$/, "Tn ch bao gm k t t a-z")
    .max(21, "Tn qu di")
    .min(6, "Tn qu ngn"),

  birthday: yup.date().required("Vui lng nhp ngy sinh"),

  email: yup
    .string()
    .email("Email khng hp l")
    .required("Vui lng nhp email"),

  studentID: yup
    .string()
    .required("Vui lng nhp m s sinh vin")
    .matches(/^([^a-z]*)$/, "M s sinh vin ch gm 7 s t 0-9")
    .max(7, "M s sinh vin bao gm 7 s")
    .min(7, "M s sinh vin bao gm 7 s"),

  phoneNumber: yup
    .string()
    .required("Vui lng nhp m s in thoi")
    .matches(/^([^a-z]*)$/, "S in thoi ch gm 10 s t 0-9")
    .max(10, "S in thoi bao gm 10 s")
    .min(10, "S in thoi bao gm 10 s"),

  password: yup
    .string()
    .min(6, "Mt khu qu ngn")
    .required("Vui lng nhp mt khu"),

  password2: yup
    .string()
    .oneOf([yup.ref("password")], "Mt khu khng ging mt khu  nhp")
    .required("Vui lng nhp li mt khu"),
});

const SignUp = (props) => {
  const dispatch = useDispatch();

  const handleSuccess = () => {
    props.history.push("/signin");
  };

  return (
    <div>
      <Formik
        initialValues={{
          fullName: "",
          birthday: "",
          email: "",
          studentID: "",
          phoneNumber: "",
          password: "",
          password2: "",
        }}
        validationSchema={validationSchema}
        onSubmit={(values) => dispatch(signUp(values, handleSuccess))}
      >
        {({ handelSubmit }) => (
          <div className="signUpBody">
            <title>ng k</title>
            <div className="toSignUp">
              <div className="formSignUp">
                <h2>ng k hc</h2>
                <img src="../img/2.png" />
                <Form className="Formik">
                  <MyInput type="text" name="fullName" label="H v tn" />
                  <MyInput
                    type="date"
                    name="birthday"
                    label="Ngy sinh"
                    format="dd/MM/yyyy"
                    InputLabelProps={{ shrink: true }}
                  />
                  <MyInput type="email" name="email" id="email" label="Email" />
                  <MyInput
                    type="text"
                    name="studentID"
                    label="M s sinh vin"
                  />
                  <MyInput
                    type="text"
                    name="phoneNumber"
                    label="S in thoi"
                  />
                  <MyInput type="password" name="password" label="Mt khu" />
                  <MyInput
                    type="password"
                    name="password2"
                    label="Nhp li mt khu"
                  />
                  <button
                    id="register1"
                    type="submit"
                    name="SignIn"
                    onClick={handelSubmit}
                  >
                    ng k
                  </button>
                </Form>
                <button
                  id="register2"
                  name="Back"
                  onClick={() => props.history.push("/signin")}
                >
                  Tr li
                </button>
              </div>
            </div>
          </div>
        )}
      </Formik>
    </div>
  );
};

export default SignUp;
